.post_title {
    padding: 1rem;
    position: relative;
    z-index: 0;
    &__overlay {
        background-color: rgba(0,0,0,0.2);
        filter: blur(1px);
        position: absolute;
        inset: 0;
        z-index: -1;
    }
    .hero__title {
        font-size: 2rem;
        font-size: clamp(2rem, 1.9rem + 0.5vw, 2.5rem);
    }
}

.grid_item {
    a {
        text-decoration: none;
        color: initial;
    }
    .news__title {
        margin-bottom: 0;
    }
    .news__date {
        font-size: 0.8rem;
        margin-bottom: 0.5rem;
    }
    .news__image_container {
        position: relative;
        z-index: 0;
        .news__flash {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            color: white;
            font-size: 0.8rem;
            padding: 0.25rem 0.5rem;
            &--news {
                background-color: var(--rbf-blue-900);
            }
            &--donate,
            &--event {
                background-color: var(--rbf-purple-900);
            }
            &--support {
                background-color: var(--rbf-green-900);
            }
        }
    }
    .card {
        margin: 1rem 0.5rem;
    }
}

