.navigation li a,
.navigation li a:hover,
.navigation li.active a,
.navigation li.disabled {
    color: white;
    text-decoration:none;
}
 
.navigation li {
    display: inline;
}
 
.navigation li a,
.navigation li a:hover,
.navigation li.active a,
.navigation li.disabled {
    background-color: $rbf-blue-900;
    border-radius: 3px;
    cursor: pointer;
    padding: 12px;
    padding: 0.75rem;
}
 
.navigation li a:hover,
.navigation li.active a {
    background-color: darken( $rbf-blue-900 ,10%);
}

